import React from "react";
import CustomDropdown from "./CustomDropdown";
import { StyledCustomerForm } from "./styles/CustomerForm.styled";
import { StyledCustomerInput } from "./styles/CustomerInput.styled";
import CustomInput from "./CustomInput";
import { CustomerHeader } from "./styles/CustomerInputHeader.styled";

function CustomerForm({ errors, control }) {
  const deliveryOptions = [
    { value: "Teslim Bir", label: "Teslim Bir" },
    { value: "Teslim ki", label: "Teslim ki" },
    { value: "Teslim ", label: "Teslim " },
  ];
  const dealerOptions = [
    { value: "Satc Bir", label: "Satc Bir" },
    { value: "Satc ki", label: "Satc ki" },
    { value: "Satc ", label: "Satc " },
  ];
  const bankOptions = [
    { value: "Banka Bir", label: "Banka Bir" },
    { value: "Banka ki", label: "Banka ki" },
    { value: "Banka ", label: "Banka " },
  ];
  const paymentMethodOptions = [
    { value: "Nakit", label: "Nakit" },
    { value: "Kredi Kart", label: "Kredi Kart" },
    { value: "Czdan", label: "Czdan" },
  ];
  return (
    <StyledCustomerForm>
      <StyledCustomerInput>
        <CustomerHeader>ALICI ADI</CustomerHeader>
        <CustomInput
          registerName={"receiverName"}
          control={control}
          placeholder={"Alc ad..."}
          errors={errors}
          type="text"
          errorMessage="Alc Ad Giriniz!"
        />
      </StyledCustomerInput>

      <StyledCustomerInput>
        <CustomerHeader>ALICI CAR KOD</CustomerHeader>
        <CustomInput
          registerName={"currentCode"}
          control={control}
          placeholder={"Cari kod..."}
          errors={errors}
          type="text"
          errorMessage="Alc Cari Kodu Giriniz!"
        />
      </StyledCustomerInput>

      <StyledCustomerInput>
        <CustomerHeader>TESLM</CustomerHeader>
        <CustomDropdown
          registerName={`delivery`}
          control={control}
          options={deliveryOptions}
          placeholder={"Seiniz..."}
          errors={errors}
          errorMessage="Teslimat Seiniz!"
        />
      </StyledCustomerInput>

      <StyledCustomerInput>
        <CustomerHeader>TESLM N ZEL KOD</CustomerHeader>
        <CustomInput
          registerName={"deliveryCode"}
          control={control}
          placeholder={"Teslim iin zel kod..."}
          errors={errors}
          type="text"
          errorMessage="Teslimat in zel Kod Giriniz!"
        />
      </StyledCustomerInput>
      <StyledCustomerInput>
        <CustomerHeader>SATICI</CustomerHeader>
        <CustomDropdown
          registerName={`dealer`}
          control={control}
          options={bankOptions}
          placeholder={"Seiniz..."}
          errors={errors}
          errorMessage="Satc Seiniz!"
        />
      </StyledCustomerInput>
      <StyledCustomerInput>
        <CustomerHeader>BANKA</CustomerHeader>
        <CustomDropdown
          registerName={`bank`}
          control={control}
          options={dealerOptions}
          placeholder={"Seiniz..."}
          errors={errors}
          errorMessage="Banka Seiniz!"
        />
      </StyledCustomerInput>
      <StyledCustomerInput>
        <CustomerHeader>DEME</CustomerHeader>
        <CustomDropdown
          registerName={`paymentMethod`}
          control={control}
          options={paymentMethodOptions}
          placeholder={"Seiniz..."}
          errors={errors}
          errorMessage="deme Yntemi Seiniz!"
        />
      </StyledCustomerInput>
      <StyledCustomerInput>
        <CustomerHeader>FATURA NO</CustomerHeader>
        <CustomInput
          registerName={"invoice"}
          control={control}
          placeholder={"Fatura no..."}
          errors={errors}
          type="text"
          errorMessage="Fatura No. Giriniz!"
        />
      </StyledCustomerInput>
    </StyledCustomerForm>
  );
}

export default CustomerForm;
