import React, { useState } from "react";
import Multiselect from "multiselect-react-dropdown";
import { StyledSelection } from "./styles/Selections.styled";
import CustomDropdown from "./CustomDropdown";
import { SelectionHeader } from "./styles/SelectionInputHeader.styled";
import { StyledSelectionInput } from "./styles/SelectionInput.styled";
import { SectionHeader } from "./styles/ProductSectionHeader.styled";

function Selections({
  index,
  errors,
  append,
  remove,
  control,
  fields,
}) {
  const [contentNames] = useState([
    "NEVRESM",
    "ARAF",
    "STD. YASTIK KILIFI",
    "OXF. YASTIK KILIFI",
    "KIRLENT KILIFI",
  ]);

  const brandDataOptions = [
    { value: "Marka Bir", label: "Marka Bir" },
    { value: "Marka ki", label: "Marka ki" },
    { value: "Marka ", label: "Marka " },
  ];

  const sizeDataOptions = [
    { value: "Beden Bir", label: "Beden Bir" },
    { value: "Beden ki", label: "Beden ki" },
    { value: "Beden ", label: "Beden " },
  ];

  const productTypeDataOptions = [
    { value: "rn Cinsi Bir", label: "rn Cinsi Bir" },
    { value: "rn Cinsi ki", label: "rn Cinsi ki" },
    { value: "rn Cinsi ", label: "rn Cinsi " },
  ];

  const packageTypeDataOptions = [
    { value: "Ambalaj Bir", label: "Ambalaj Bir" },
    { value: "Ambalaj ki", label: "Ambalaj ki" },
    { value: "Ambalaj ", label: "Ambalaj " },
  ];

  const registerName = `products[${index}]`;

  const customStyles = {
    control: (styles) => ({
      ...styles,
      backgroundColor: "white",
      
      border: "1px solid black"
    }),
    valueContainer: (state) => ({
      ...state,
    }),

    input: (state) => ({
      ...state,
    }),

    indicatorsContainer: (state) => ({
      ...state,
    }),
    option: (styles, { data, isDisabled, isFocused, isSelected }) => {
      return { ...styles };
    },
    singleValue: (styles, { data }) => {
      return { ...styles };
    },
  };

  return (
    <StyledSelection>
      <SectionHeader>SEMLER</SectionHeader>

      <StyledSelectionInput>
        <SelectionHeader>Marka:</SelectionHeader>
        <CustomDropdown
          registerName={`${registerName}.brand`}
          customStyles={customStyles}
          control={control}
          options={brandDataOptions}
          placeholder={"Marka Seiniz..."}
          errors={errors}
          errorMessage="Marka Seiniz!"
        />
      </StyledSelectionInput>

      <StyledSelectionInput>
        <SelectionHeader>Beden:</SelectionHeader>
        <CustomDropdown
          registerName={`${registerName}.size`}
          customStyles={customStyles}
          control={control}
          options={sizeDataOptions}
          placeholder={"Beden Seiniz..."}
          errors={errors}
          errorMessage="Beden Seiniz!"
        />
      </StyledSelectionInput>
      <StyledSelectionInput>
        <SelectionHeader>rn Cinsi:</SelectionHeader>
        <CustomDropdown
          registerName={`${registerName}.productType`}
          customStyles={customStyles}
          control={control}
          options={productTypeDataOptions}
          placeholder={"rn Cinsi Seiniz..."}
          errors={errors}
          errorMessage="rn Cinsi Seiniz!"
        />
      </StyledSelectionInput>
      <StyledSelectionInput>
        <SelectionHeader>erikler:</SelectionHeader>
        <Multiselect
          options={contentNames}
          onKeyPressFn={function noRefCheck() {}}
          onSearch={function noRefCheck() {}}
          isObject={false}
          onRemove={(selectedList, itemtobeRemoved) => {
            remove(
              fields.map((item) => item.contentName).indexOf(itemtobeRemoved)
            );
          }}
          onSelect={(selectedList, selectedItem) => {
            append({
              contentName: selectedItem,
            });
          }}
          placeholder="erik Se"
          avoidHighlightFirstOption={true}
          showCheckbox
          displayValue="value"
          style={{
            background: "white",
            searchBox: {
              
              minHeight: "38px",
              border: "1px solid #374151",
              background: "white",
              
            },
            inputField: {
              fontSize: "16px",
            }
          }}
        />
      </StyledSelectionInput>
      <StyledSelectionInput>
        <SelectionHeader>Ambalaj:</SelectionHeader>
        <CustomDropdown
          registerName={`${registerName}.packageType`}
          customStyles={customStyles}
          control={control}
          options={packageTypeDataOptions}
          placeholder={"Ambalaj Seiniz..."}
          errors={errors}
          errorMessage="Ambalaj Seiniz!"
        />
      </StyledSelectionInput>
    </StyledSelection>
  );
}

export default Selections;
